
// axios
import axios from 'axios';
// 
axios.defaults.headers = {
  'X-Resquested-With': 'XMLHttpRequest',
};

// process.env.BASE_URL

// axios.defaults.baseURL = 'http://192.168.11.220:4000';

const errMsgObj = {
  400: '',
  401: '',
  403: '',
  404: ',',
  405: '',
  408: '',
  500: '',
  501: '',
  502: '',
  503: '',
  504: '',
  505: 'http',
  undefined: '',
};

// 
axios.defaults.timeout = 30000;
// 
// config
axios.interceptors.request.use((config) => config, (error) => Promise.reject(error));

// 
axios.interceptors.response.use((config) => config,
  (error) => {
    const wrong = error;
    // 
    if (wrong && wrong.response) {
      const { data, status } = wrong.response;
      if (data && data.message) {
        wrong.message = data.message;
      } else {
        wrong.message = errMsgObj[status];
      }
      if (status === 401) {
        window.history.back();
      }
    } else {
      wrong.message = '';
    }
    return Promise.reject(wrong);
  });

export default axios;
