<template>
  <div class="picture-spinning" v-loading="loading">
    <div class="picture-control vertical-align-center">
      <div :class="['picture-button', 'vertical-align-center', {
        'disabled': (total === 0) || (index - 1 < 0)
      }]" @click="pageTurn(-1)">&#60;</div>
      <div :class="['picture-button', 'vertical-align-center', {
        'disabled': (total === 0) || (index + 1 === total)
      }]" @click="pageTurn(1)">&#62;</div>
    </div>
  </div>
</template>

<script>
export default {
  methods: {
    pageTurn(val) {
      this.$emit('pageTurn', val);
    },
  },
  props: {
    loading: Boolean,
    total: Number,
    index: Number,
  },
};
</script>

<style lang="less">
  .picture-spinning {
    .ant-spin {
      max-height: none !important;
    }
    .ant-spin-container {
      height: 100%;
    }
  }
</style>
<style scoped lang="less">
  .picture-spinning {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    .picture-control {
      height: 100%;
      justify-content: space-between;
      .picture-button {
        height: 115px;
        width: 45px;
        font-size: 32px;
        color: #fff;
        background: #000;
        padding-bottom: 4px;
        border-radius: 4px;
        cursor: pointer;
        opacity: .4;
        justify-content: center;
        user-select: none;
        &:hover {
          opacity: .8;
        }
      }
      .disabled {
        pointer-events: none;
        cursor: default;
        opacity: 0.6;
      }
    }
  }
  .vertical-align-center {
    display: flex;
    align-items: center;
  }
</style>
