<template>
  <div class="diagnosis">
    <div class="left" v-loading="treeLoading">
      <div class="title"></div>
      <div class="filter">
        <el-input
          placeholder=""
          v-model="filterText">
        </el-input>
        <el-tree
          class="filter-tree"
          :data="TreeData"
          :props="defaultProps"
          :filter-node-method="filterNode"
          @node-click="handleNodeClick"
          highlight-current
          :expand-on-click-node="false"
          :default-expand-all="false"
          :accordion="true"
          ref="tree">
        </el-tree>
      </div>
    </div>
    <div class="right">
      <el-button type="primary"
                 :loading="loading || status !== ''"
                 :disabled="label === ''"
                 @click="createFn">
        {{history[label] ? '' : ''}}
      </el-button>
      <el-progress :percentage="percentage"></el-progress>
      <div class="images-content">
        <div class="images">
          <picture-control @pageTurn="pageTurnFn"
                           :index="index"
                           :total="photoAlbum.length"/>
          <img :src="src"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import cases from '@/api/cases';
import tasks from '@/api/tasks';
import pictureControl from '@/components/picture_control.vue';

export default {
  components: {
    pictureControl,
  },
  data() {
    return {
      label: '',
      filterText: '',
      src: '',
      taskId: '',
      status: '',
      index: 0,
      percentage: 0,
      treeLoading: false,
      loading: false,
      interval: null,
      TreeData: [],
      photoAlbum: [],
      history: {},
      defaultProps: {
        children: 'children',
        label: 'filename',
      },
      statusInfo: {
        PENDING: '',
        PROGRESS: '',
        SUCCESS: '',
      },
    };
  },
  mounted() {
    this.searchCasesFn();
  },
  watch: {
    filterText(val) {
      this.$refs.tree.filter(val);
    },
    status(val) {
      if (val) this.$message.info(this.statusInfo[val]);
    },
  },
  beforeDestroy() {
    this.clearFn();
  },
  methods: {
    filterNode(value, data) {
      if (!value) return true;
      return data.filename.indexOf(value) !== -1;
    },
    handleNodeClick({ casename }) {
      this.label = casename;
      this.resetFn();
      this.clearFn();
      if (this.loading) this.loading = false;
      if (this.history[casename]) {
        this.taskId = this.history[casename];
        this.loading = true;
        this.loopFn();
      }
    },
    resetFn() {
      this.src = '';
      this.index = 0;
      this.percentage = 0;
      this.photoAlbum = [];
    },
    pageTurnFn(val) {
      this.index += val;
      this.src = `http://${window.location.host}/api/images/${this.photoAlbum[this.index]}`;
      // this.src = `http://192.168.11.220:5001/api/images/${this.photoAlbum[this.index]}`;
    },
    searchCasesFn() {
      this.treeLoading = true;
      cases.getCases().then(({ data }) => {
        try {
          this.TreeData = data.data;
        } catch (e) {
          console.log(e);
        }
        this.treeLoading = false;
      }).catch(() => {
        this.treeLoading = false;
      });
    },
    createFn() {
      this.resetFn();
      this.loading = true;
      tasks.create({
        caseName: this.label,
        force: this.history[this.label] ? 1 : 0,
      }).then(({ data }) => {
        try {
          this.history[this.label] = data.task_id;
          this.taskId = data.task_id;
          this.loopFn();
        } catch (e) {
          console.log(e);
        }
      }).catch(() => {
        this.loading = false;
      });
    },
    clearFn() {
      this.status = '';
      clearInterval(this.interval);
    },
    loopFn() {
      this.interval = setInterval(() => {
        tasks.getResult(this.taskId).then(({
          data: { data: { current, state, result } },
        }) => {
          console.log(current, state);
          if (this.loading) this.loading = false;
          try {
            this.status = state;
            this.percentage = current;
            if (state === 'SUCCESS') {
              this.clearFn();
              this.photoAlbum = result.images;
              this.src = `http://${window.location.host}/api/images/${this.photoAlbum[this.index]}`;
              // this.src = `http://192.168.11.220:5001/api/images/${this.photoAlbum[this.index]}`;
            }
          } catch (e) {
            console.log(e);
          }
        });
      }, 2000);
    },
  },
};
</script>

