
// api
import { API_URL } from '@/constants/';
import axios from './axios';

export default {
  create(data) {
    return axios.post(`${API_URL}/tasks`, data)
      .then((res) => res.data);
  },
  getResult(id) {
    return axios.get(`${API_URL}/tasks/${id}/result`).then((res) => ({
      data: res.data,
    }));
  },
};
